.main-div {
    position: relative;
    width: 50%;
  }
  
  .shade-div {
    opacity: 0;
    display: block;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    background-color:#121212;
    backface-visibility: hidden;
  }
  
#divbutton{
    transition: .5s ease !important;
    opacity: 1;
    height:100%;
    width:100%;
    margin:0 !important;
    font-size:1.5em !important;
    font-weight:900 !important;
  }
  
  .main-div:hover .shade-div {
    opacity: 0.8;
  }
  
