.intro-div{
    font-size:2em;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    margin-bottom:0;
}

.background-image{
    position:absolute;
    height:100vh;
    width:100vw;
    background-image:url('http://getwallpapers.com/wallpaper/full/2/7/a/1113171-widescreen-cool-space-background-wallpapers-1920x1200.jpg');   background-repeat: no-repeat;
    background-size:cover;
}

.filter-gradient-gray{
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: block;
    background-color:#121212;
    opacity: .5;

}

.intro-text{
    font-size: 1.5em;
    color: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width:100%;
}

.chevron-container {
    position:absolute;
    bottom:15px;
    width:100%;
  }

  
  .chevron {
    position: absolute;
    width: 28px;
    height: 8px;
    opacity: 0;
    transform: scale3d(0.5, 0.5, 0.5);
    animation: move 3s ease-out infinite;
    left: calc(50% - 14px);
  }
  
  .chevron:first-child {
    animation: move 3s ease-out 1s infinite;
  }
  
  .chevron:nth-child(2) {
    animation: move 3s ease-out 2s infinite;
  }
  
  .chevron:before,
  .chevron:after {
    content: ' ';
    position: absolute;
    top: 0;
    height: 100%;
    width: 51%;
    background: #fff;
  }
  
  .chevron:before {
    left: 0;
    transform: skew(0deg, 30deg);
  }
  
  .chevron:after {
    right: 0;
    width: 50%;
    transform: skew(0deg, -30deg);
  }
  
  @keyframes move {
    25% {
      opacity: 1;
  
    }
    33% {
      opacity: 1;
      transform: translateY(30px);
    }
    67% {
      opacity: 1;
      transform: translateY(40px);
    }
    100% {
      opacity: 0;
      transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
    }
  }
  
  .text {
    display: block;
margin-top: 75px;
font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
font-size: 12px;
color: #fff;
text-transform: uppercase;
white-space: nowrap;
opacity: .25;
animation: pulse 2s linear alternate infinite;
left: 50%;
text-align: center;
width: 100%;
  }
  
  @keyframes pulse {
    to {
      opacity: 1;
    }
}